<template>
    <div class="records">
        <t-header :title="$t('doc.generate_record')" info=""></t-header>
        <div class="content">
            <div class="no_record" v-if="recordList ===''">
                <span>{{$t('doc.no_record')}}</span>
            </div>
            <div class="record-list" v-else>
                <ul>
                    <li>1233</li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>

import THeader from '@/components/THeader'
export default {
    components: {
        THeader,
    },
    data(){
        return {
            recordList: '',
        }
    },
    created(){
        // this.recordList=1
    }
}
</script>
<style scoped>
    .records .content{
        padding: 0 10px;
        margin-top: 50px;
    }
    .no_record{
        text-align: center;
        padding-top: 30px;
        color: #CDCDCD;
    }
    .record-list{
        height: 100%;
    }
    .record-list ul li{
        padding: 10px 0;
        border-bottom: 1px solid #cdcdcd;
    }
</style>






