<template>
    <div class="section home">
        <div class="header">
            <div class="top">
                <div class="top_fl">
                    <i class="iconfont icon-tubiaolunkuo- icon-color"></i>
                    <span>{{$t('doc.scan_code')}}</span>
                </div>
                <div class="top_fr">
                    <i class="iconfont icon-erweima icon-color"></i>
                    <i class="iconfont icon-duorenduihua icon-color"></i>
                </div>
            </div>
            <div class="user_bg">
                <div class="user">
                    <div class="circle">
                        <img :src="imgUrl" alt="">
                    </div>
                    <div class="username">
                        <span>用户名</span>
                        <i class="iconfont icon-vip1"></i>
                     </div>
                </div>
            </div>
        </div>
        <div class="property">
            <ul>
                <li>
                    <span class="token">EP</span> 
                    <span>11111</span>
                </li>
                <li>
                    <span class="token-">{{$t('doc.user_property')}}</span> 
                    <span>1111</span>
                </li>
            </ul>
        </div>
        <div class="nav">
            <ul>
                <li>
                    <span class="zone-a">{{$t('doc.a_zone')}}</span> 
                    <span>11111</span>
                </li>
                <li>
                    <span class="zone">TFC</span> 
                    <span>22222</span>
                </li>
                <li>
                    <span class="zone-b">{{$t('doc.b_zone')}}</span> 
                    <span>33333</span>
                </li>
            </ul>
        </div>
        <div class="list">
            <ul>
                <router-link :to="{name:'transfer'}" tag="li">    
                    <i class="iconfont icon-71 icon-list"></i>
                    <span>{{$t('doc.user_transfer')}}</span>
                </router-link>
                <router-link :to="{name:'trade'}" tag="li"> 
                    <i class="iconfont icon-zichanliebiao icon-list"></i>
                    <span>{{$t('doc.user_trade')}}</span>
                </router-link>
                <router-link :to="{name:'wallet'}" tag="li">
                    <i class="iconfont icon-securityCode-b icon-list"></i>
                    <span>{{$t('doc.user_wallet')}}</span>
                </router-link>
                <router-link :to="{name:'register'}" tag="li">
                    <i class="iconfont icon-c-xinrenzhuce icon-list"></i>
                    <span>{{$t('doc.register')}}</span>
                </router-link>
                <router-link :to="{name:'activate'}" tag="li">
                    <i class="iconfont icon-msnui-active icon-list"></i>
                    <span>{{$t('doc.user_activate')}}</span>
                </router-link>
                <router-link :to="{name:'setting'}" tag="li">
                    <i class="iconfont icon-weibiaoti103 icon-list"></i>
                    <span>{{$t('doc.user_setting')}}</span>
                </router-link>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            imgUrl: require('@/assets/images/img_user.png')
        }
    }
}
</script>

<style lang="scss" scoped>
    @import '../../assets/css/mixin';
    .home{
        background: #F8F8F8;
        height: 100vh;
    }
    .home .header{
        background: $mtBgHeader;
        background-size: cover;
        background-position: 50%;
        height: 297px;
    }
    .top{
        height: 50px;
        padding: 0 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
    }
    .top span{
        color: $mtWhite;
    }
    .icon-color{
        font-size: 0.2rem;
        color: $mtWhite;
    }
    .header .user_bg{
        width: 105px;
        height: 150px;
        margin: 0 auto;
    }
    .user{
        min-height: 1px;
        position: relative;
    }
    .user::after{
        content: '';
        width: 229px;
        height: 105px;
        border-radius: 10px;
        background-color: rgba(255,255,255,0.1);
        position: absolute;
        left: -60%;
        top: 36%;
    }
    .user .circle{
        width: 105px;
        width: 105px;
        border-radius: 50%; 
        overflow: hidden;
    }
    .circle img{
        max-width: 105px;
    }
    .username{
        margin-top: 14px;
        text-align: center;
    }
    .username span{
        color: $mtWhite;
        font-size: 0.16rem;
    }
    .username .iconfont{
        color: yellow;
        font-size: 0.18rem;
    }
    .property{
        margin-top: -60px;
    }
    .property ul{
        width: 100%;
        height: 44px; 
        display: flex;
    }
    .property ul li{
        width: 50%;
        height: 44px;
        display: flex;
        align-items: center; 
        color: $mtWhite;
        font-weight: 520;
    }
    .property ul li .token{
        margin-left: 30px;
        margin-right: 40px;
    }
    .property ul li .token-{
        margin-left: 30px;
        margin-right: 40px;
    }
    .property li:first-child{
        background-color: $mtYellow;
    }
    .property li:last-child{
        background-color: $mtBlue;
    }
    .nav{
        padding: 0 10px;
        height: 52px;
        background: $mtWhite;
        margin-bottom: 10px;
    }
    .nav ul{
        display: flex;
        height: 52px;
    }
    .nav ul li{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 33%;
        border-right: $mtBorder;
    }
    .nav ul li:last-child{
        border-right: none;
    }
    .list{
        background: $mtWhite;
        padding: 0 10px;
    }
    .list ul{
        display: flex;
        flex-wrap: wrap;
    }
    /* li标签的50%不生效 是没有在父级加换行 */
    .list ul li{
        width: 33%;
        height: 100px;
        border-right: $mtBorder; 
        border-bottom: $mtBorder; 
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        color: $mtBlue;
    }
    .list ul li:nth-child(3n){
        border-right: none;
    }
    .list ul li:nth-child(4),.list ul li:nth-child(5),.list ul li:nth-child(6){
        border-bottom: none;
    }
    .icon-list{
        font-size: 0.26rem;
        margin-bottom: 10px;
    }
</style>




