<template>
    <div class="section">
        <div class="header">
            <t-header :title="$t('doc.user_setting')" info=""></t-header>
        </div>
        <div class="user_bg">
            
        </div>
        <div class="user">
            <div class="circle">
                <img :src="imgUrl" alt="">
            </div>
            <div class="username">
                <span class="user_name">老船长</span>
                <span class="user_tel">1111111111</span>
            </div>
        </div>
        <div class="list">
            <ul>
                <router-link :to="{name: 'modifyName'}" tag='li'>
                    <span>{{$t('doc.nickname')}}</span>
                    <span class="arrow">老船长</span>
                </router-link>
                <router-link :to="{name: ''}" tag='li'>
                    <span>{{$t('doc.language')}}</span>
                    <span class="arrow">{{$t('doc. change_language')}}</span>
                </router-link>
                <router-link :to="{name: 'bankCard'}" tag='li'>
                    <span>{{$t('doc.bank_card')}}</span>
                    <span class="arrow">0</span>
                </router-link>
                <router-link :to="{name: 'modifyLoginPwd'}" tag='li'>
                    <span>{{$t('doc.login_pwd')}}</span>
                    <span class="arrow">{{$t('doc.click_modify')}}</span>
                </router-link>
                <router-link :to="{name: 'modifyPayPwd'}" tag='li'>
                    <span>{{$t('doc.pay_pwd')}}</span>
                    <span class="arrow">{{$t('doc.click_modify')}}</span>
                </router-link>
                <router-link :to="{name: ''}" tag='li'>
                    <span>{{$t('doc.version')}}</span>
                    <span class="arrow">1.0.6</span>
                </router-link>
            </ul>
        </div>
        <div class="footer">
            <router-link :to="{name: 'login'}" tag='div' class="login_out" >
                {{$t('doc.login_out')}}
            </router-link>
        </div>
    </div>
</template>
<script>
import THeader from '@/components/THeader'
export default {
    components:{
        THeader
    },
    data(){
        return {
            imgUrl: require('@/assets/images/img_user.png')
        }
    }
}
</script>
<style lang="scss" scoped>
    @import "../../assets/css/mixin";
    .header{
        background: $mtBg;
        background-size: cover;
        background-position: 50%; 
        height: 300px;
    }
    .user_bg{
        width: 90%;
        height: 139px;
        background: linear-gradient(#FFFFFF,#f0f0f0);
        border-radius: 10px;
        margin: 0 auto;
        margin-top: -130px;
    }
    .user{
        width: 105px;
        height: 200px;
        margin: 0 auto;
        margin-top: -190px;
    }
    .user .circle{
        width: 105px;
        height: 105px;
        border-radius: 50%; 
        overflow: hidden;
        margin-bottom: 14px;   
    }
    .circle img{
        max-width: 105px;
    }
    .user .username{
        height: 60px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .user .user_name{
        color: $mtDarkBlue;
        font-size: 0.16rem;
        font-weight: 500;
    }
    .user .user_tel{
        color: $mtDarkBlue;
        font-size: 0.16rem;
        font-weight: 500;
    }
    .list ul{
        padding: 0 20px;
    }
    .list ul li{
        display: flex;
        color: $mtDarkBlue;
        justify-content: space-between;
        border-bottom: 1px solid #f0f0f0;
        padding: 12px 0;
    }
    .arrow::after{
        content: '';
        display: inline-block;
        width: 8px;
        height: 8px;
        border-top: 1px solid #888;
        border-right: 1px solid #888;
        transform: rotate(45deg);
        margin-left: 6px;
    }
    .footer{
        height: 50px;
        text-align: center;
        line-height: 50px;
        color: $mtDarkBlue;

    }
</style>





