<template>
    <div>
        <t-header :title="$t('doc.modify_pay_pwd')" info=""></t-header>
        <div class="content">
            <div class="form_wrapper">
                <form action="">
                    <div class="con_row">
                        <input type="text" :placeholder="$t('doc.old_pay_pwd')" v-model="ruleForm.oldPwd">
                    </div>
                    <div class="con_row">
                        <input type="text" :placeholder="$t('doc.new_pay_pwd')" v-model="ruleForm.newPwd">
                    </div>
                    <div class="forgot">
                        <router-link :to="{name:'retrievePayPwd'}" tag="span">
                            {{$t('doc.forgot_pay_pwd')}} ？
                        </router-link>
                    </div>
                    <div class="sumbit" @click="sumbit">{{$t('doc.confirm')}}</div>
                </form>
            </div>
        </div>
    </div>
</template>
<script>
import THeader from '@/components/THeader'
export default {
    components: {
        THeader,
    },
    data(){
        return {
            ruleForm: {
                oldPwd: '',
                newPwd: '',
            }
        }
    },
    methods: {
        sumbit(){
            //axios
        }
    }
}
</script>
<style lang="scss" scoped>
    @import "../../assets/css/mixin";
    .content{
        background-color: $mtWhite;
        margin-top: 50px;
    }
    .content .form_wrapper{
        padding: 0 20px;
    }
    .con_row{
        display: flex;
        flex-direction: column;
        border-bottom: $mtBorder;
        justify-content: center;
    }
    input::-webkit-input-placeholder {
        color: #9B9B9B;
        font-size: 0.18rem;
    }
    .con_row input{
        padding: 20px 0;
        font-size: 0.18rem;
    }
    .forgot{
        margin-top: 10px; 
        overflow: hidden;   
    }
    .forgot span{
        float: right;
        color: $mtBlue;
    }
    .sumbit{
        width:96%;
        height: 52px;
        background: $mtBgYellow;
        margin: 0 auto;
        margin-top: 37px;
        border-radius: 8px;
        text-align: center;
        line-height: 52px;
        color: $mtWhite;
        font-size: 0.18rem;
    }
</style>





