
import Login from '@/views/login/Login'
import Home from '@/views/home/Home'
//转账
import Transfer from '@/views/transfer/Transfer'
import Transfers from '@/views/transfer/Index'
import TransferRecord from '@/views/transfer/TransferRecord'
import Exchange from '@/views/transfer/Exchange'
import ExchangeRecord from '@/views/transfer/ExchangeRecord'
//交易
import Trade from '@/views/trade/Trade'
//钱包
import Wallet from '@/views/wallet/Wallet'
import Wallets from '@/views/wallet/Index'
import TurnOut from '@/views/wallet/TurnOut'
import WithdrawRecord from '@/views/wallet/WithdrawRecord'
//注册
import Register from '@/views/register/Register'
import UserRegister from '@/views/register/UserRegister'
//激活卡
import Activate from '@/views/activate/Activate'
import Activated from '@/views/activate/Index'
import ActivateRecord from '@/views/activate/ActivateRecord'
import CardFeed from '@/views/activate/CardFeed'
import FeedRecord from '@/views/activate/FeedRecord'
import GenerateCard from '@/views/activate/GenerateCard'
import GenerateRecord from '@/views/activate/GenerateRecord'
//个人设置
import Setting from '@/views/settings/Setting'
import Settings from '@/views/settings/Index'
import ModifyName from '@/views/settings/ModifyName'
import BankCard from '@/views/settings/BankCard'
import AddBankCard from '@/views/settings/AddBankCard'
import ModifyLoginPwd from '@/views/settings/ModifyLoginPwd'
import RetrieveLoginPwd from '@/views/settings/RetrieveLoginPwd'
import ModifyPayPwd from '@/views/settings/ModifyPayPwd'
import RetrievePayPwd from '@/views/settings/RetrievePayPwd'

export default [
  {
    path: '/',
    component: Login
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/home',
    name: 'home',
    component: Home
  },
  {
    path: '/transfer',
    name: 'transfer',
    component: Transfer
  },
  {
    path: '/transfers',
    name: 'transfers',
    component: Transfers,
    children:[
      {
        path:'/transfers/record',
        name: 'transferRecord',
        component: TransferRecord
      },
      {
        path:'/transfers/exchange',
        name: 'exchange',
        component: Exchange
      },
      {
        path:'/transfers/exchangeRecord',
        name: 'exchangeRecord',
        component: ExchangeRecord
      }
    ]
  },
  {
    path: '/trade',
    name: 'trade',
    component: Trade
  },
  {
    path: '/wallet',
    name: 'wallet',
    component: Wallet
  },
  {
    path: '/wallets',
    name: 'wallets',
    component: Wallets,
    children:[
      {
        path:'/wallets/turnOut',
        name: 'turnOut',
        component: TurnOut
      },
      {
        path:'/wallets/record',
        name: 'withdrawRecord',
        component: WithdrawRecord
      }
    ]
  },
  {
    path: '/register',
    name: 'register',
    component: Register
  },
  {
    path: '/userRegister',
    name: 'userRegister',
    component: UserRegister
  },
  {
    path: '/activate',
    name: 'activate',
    component: Activate
  },
  {
    path: '/activated',
    name: 'activated',
    component: Activated,
    children:[
      {
        path:'/activated/record',
        name: 'activateRecord',
        component: ActivateRecord
      },
      {
        path:'/activated/cardFeed',
        name: 'cardFeed',
        component: CardFeed
      },
      {
        path:'/activated/feedRecord',
        name: 'feedRecord',
        component: FeedRecord
      },
      {
        path:'/activated/generateCard',
        name: 'generateCard',
        component: GenerateCard
      },
      {
        path:'/activated/generateRecord',
        name: 'generateRecord',
        component: GenerateRecord
      },
    ]
  },
  {
    path: '/setting',
    name: 'setting',
    component: Setting
  },
  {
    path: '/settings',
    name: 'settings',
    component: Settings,
    children:[
      {
        path:'/settings/modifyName',
        name: 'modifyName',
        component: ModifyName
      },
      {
        path:'/settings/bankCard',
        name: 'bankCard',
        component: BankCard
      },
      {
        path:'/settings/addBankCard',
        name: 'addBankCard',
        component: AddBankCard
      },
      {
        path:'/settings/modifyLoginPwd',
        name: 'modifyLoginPwd',
        component: ModifyLoginPwd
      },
      {
        path:'/settings/retrieveLoginPwd',
        name: 'retrieveLoginPwd',
        component: RetrieveLoginPwd
      },
      {
        path:'/settings/modifyPayPwd',
        name: 'modifyPayPwd',
        component: ModifyPayPwd
      },
      {
        path:'/settings/retrievePayPwd',
        name: 'retrievePayPwd',
        component: RetrievePayPwd
      },
    ]
  },
  {
    path: '*',
    component: Login
  },
]



