<template>
    <div>
        <t-header :title="$t('doc.retrieve_login_pwd')" info=""></t-header>
        <div class="content">
            <div class="form_wrapper">
                <form action="">
                    <div class="con_row">
                        <input type="text" v-model="ruleForm.oldPwd">
                    </div>
                    <div class="con_row verify">
                        <input type="text" :placeholder="$t('doc.enter_verify_code')" v-model="ruleForm.newPwd">
                        <span class="verify_code" @click="getCode">{{$t('doc.verify_code')}}</span>
                    </div>
                    <div class="con_row">
                        <input type="text" :placeholder="$t('doc.set_new_password')" v-model="ruleForm.oldPwd">
                    </div>
                    <div class="sumbit" @click="sumbit">{{$t('doc.confirm')}}</div>
                </form>
            </div>
        </div>
    </div>
</template>
<script>
import THeader from '@/components/THeader'
export default {
    components: {
        THeader,
    },
    data(){
        return {
            ruleForm: {
                oldPwd: '',
                newPwd: '',
            }
        }
    },
    methods: {
        getCode(){

        },
        sumbit(){
            //axios
        }
    }
}
</script>
<style lang="scss" scoped>
    @import "../../assets/css/mixin";
    .content{
        background-color: $mtWhite;
        margin-top: 50px;
    }
    .content .form_wrapper{
        padding: 0 20px;
    }
    .con_row{
        display: flex;
        border-bottom: $mtBorder;
    }
    input::-webkit-input-placeholder {
        color: #9B9B9B;
        font-size: 0.18rem;
    }
    .con_row input{
        padding: 20px 0;
        font-size: 0.18rem;
    }
    .verify{
        justify-content: space-between;
        align-items: center;
    }
    .verify input{
        width: 70%;
    }
    .verify_code{
        display: block;
        padding: 7px 18px;
        background: $mtBgYellow;
        border-radius: 6px;
        color: $mtWhite;
    }
    .sumbit{
        width:96%;
        height: 52px;
        background: $mtBgYellow;
        margin: 0 auto;
        margin-top: 37px;
        border-radius: 8px;
        text-align: center;
        line-height: 52px;
        color: $mtWhite;
        font-size: 0.18rem;
    }
</style>





