<template>
    <div class="section">
        <div class="header">
            <div class="top">
                <span class="iconfont icon-xiazai6" @click="goback"></span>
                <span class="title">{{$t('doc.user_wallet')}}</span>
                <span class="iconfont icon-tubiaolunkuo-" @click="scan"></span>
            </div>
        </div>
        <div class="content">
            <div class="form_wrapper">
                <div class="two_code">

                </div>
                <div class="con_row">
                    <span>{{$t('doc.wallet_add')}}</span>
                    <div class="address">
                        <span>后台生成的地址</span>
                        <span class="copy">{{$t('doc.copy')}}</span>
                    </div>
                </div>
                <div class="sumbit" @click="sumbit">{{$t('doc.turn_out')}}</div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            ruleForm: {
                number:'',
                address:'',
            }
        }
    },
    methods:{
        goback(){
            this.$router.go(-1)
        },
        scan(){

        },
        sumbit(){
            //axios请求
            this.$router.push({name:'turnOut'})

        }
    }
}
</script>
<style lang="scss" scoped>
    @import "../../assets/css/mixin";
    .header{
        background: $mtBgHeader;
        background-size: cover;
        background-position: 50%; 
        height: 300px;
    }
    .header .top{
        height: 50px;
        padding: 0 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #f0f0f0;
    }
    .top .icon-tubiaolunkuo-{
        font-size: 0.2rem;
        color: #000;
    }
    .content{
        background: linear-gradient(#fff,#f0f0f0);
        width: 90%;
        height: 500px;
        border-radius: 10px;
        display: flex;
        margin: 0 auto;
        margin-top: -180px;
    }
    .content .form_wrapper{
        padding: 0 20px;
        width: 100%;
    }
    .two_code{
        width: 245px;
        height: 245px;
        border: 1px dashed #000;
        margin: 0 auto;
        margin-top: 40px;
    }
    .con_row{
        height: 113px;
        display: flex;
        flex-direction: column;
        border-bottom: $mtBorder;
        justify-content: center;
    }
    .con_row span{
        color: $mtBlue;
    }
    .con_row .address{
        padding: 10px 0;
        border-bottom: 1px solid #888;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .address .copy{
        background: $mtBgYellow;
        border-radius: 4px;
        color: $mtWhite;
        padding: 4px 7px;
    }
    .sumbit{
        width:96%;
        height: 52px;
        background: $mtBgYellow;
        margin: 0 auto;
        margin-top: 20px;
        border-radius: 8px;
        text-align: center;
        line-height: 52px;
        color: $mtWhite;
        font-size: 0.18rem;
    }
</style>





