<template>
    <div class="section">
        <div class="header">
            <div class="top">
                <span class="iconfont icon-xiazai6" @click="goback"></span>
                <span class="title">{{$t('doc.credits_exchange')}}</span>
                <span class="record" @click="record">{{$t('doc.exchange_record')}}</span>
            </div>
            <div class="ep_credits">
                <span>EP</span>
                <span>{{credits}}</span>
            </div>
        </div>
        <div class="content">
            <div class="form_wrapper">
                <form action="">
                    <div class="con_row">
                        <span>{{$t('doc.convert_assets')}}</span>
                        <input type="text" :placeholder="$t('doc.exchange_amount')" v-model="ruleForm.number">
                    </div>
                    <span class="prompt">{{$t('doc.exchange_prompt')}}</span>
                    <div class="sumbit" @click="sumbit">{{$t('doc.confirm')}}</div>
                </form>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            credits: '后台',
            ruleForm: {
                number:'',
                address:'',
            }
        }
    },
    methods:{
        goback(){
            this.$router.go(-1)
        },
        record(){
            this.$router.push({name: 'exchangeRecord'})
        },
        sumbit(){
            //axios请求
        },
        exchange(){
            //直接跳转到兑换页面
        }
    }
}
</script>
<style lang="scss" scoped>
    @import "../../assets/css/mixin";
    .header{
        background: $mtBgHeader;
        background-size: cover;
        background-position: 50%; 
        height: 300px;
    }
    .header .top{
        height: 50px;
        padding: 0 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #f0f0f0;
    }
    .top .record{
        font-size: 0.12rem;
    }
    .header .ep_credits{
        padding: 50px 10px 0;
        display: flex;
        color: $mtWhite;
        flex-direction: column;
        align-items: center;
    }
    .ep_credits span{
        margin-bottom: 10px;
    }
    .content{
        margin-top: -100px;
        background-color: $mtWhite;
    }
    .content .form_wrapper{
        padding: 0 20px;
    }
    .con_row{
        height: 113px;
        display: flex;
        flex-direction: column;
        border-bottom: $mtBorder;
        justify-content: center;
        margin-bottom: 20px;
    }
    .con_row span{
        color: $mtBlue;
    }
    input::-webkit-input-placeholder {
        color: #9B9B9B;
        font-size: 0.2rem;
    }
    .con_row input{
        padding: 20px 0 0 0;
        font-size: 0.2rem;
    }
    .prompt{
        color: #FFA900;
        font-size: 0.12rem;
    }
    .sumbit{
        width:96%;
        height: 52px;
        background: $mtBgYellow;
        margin: 0 auto;
        margin-top: 37px;
        border-radius: 8px;
        text-align: center;
        line-height: 52px;
        color: $mtWhite;
        font-size: 0.18rem;
    }
</style>





