<template>
    <div class="head">
        <span class="iconfont icon-xiazai6" @click="goback"></span>
        <span class="title">{{title}}</span>
        <span class="info">{{info}}</span>
    </div>
</template>
<script>
export default {
    name: 'theader',
    methods:{
        goback(){
            this.$router.go(-1)
        }
    },
    props: ['title','info']
}
</script>
<style scoped>
    .head{
        width: 100%;
        height: 50px;
        box-sizing: border-box;
        padding: 0 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #f0f0f0;
        position: fixed;
        left: 0;
        top: 0;
    }
</style>





