<template>
    <div>
        <t-header :title="$t('doc.add_bank_card')" info=""></t-header>
        <div class="content">
            <div class="form_wrapper">
                <form action="">
                    <div class="con_row">
                        <input type="text" v-model="ruleForm.number">
                    </div>
                    <div class="con_row">
                        <input type="text" :placeholder="$t('doc.bank_card_number')" v-model="ruleForm.number">
                    </div>
                    <div class="con_row">
                        <input type="text" :placeholder="$t('doc.bank_deposit')" v-model="ruleForm.number">
                    </div>
                    <div class="con_row">
                        <input type="text" :placeholder="$t('doc.bank_branch')" v-model="ruleForm.number">
                    </div>
                    <span class="prompt">{{$t('doc.bank_prompt')}}</span>
                    <div class="sumbit" @click="sumbit">{{$t('doc.confirm')}}</div>
                </form>
            </div>
        </div>
    </div>
</template>
<script>
import THeader from '@/components/THeader'
export default {
    components: {
        THeader,
    },
    data(){
        return {
            ruleForm: {
                number:'',
            }
        }
    },
    methods: {
        sumbit(){
            //axios
            this.$router.push({name: ''})
        }
    }
}
</script>
<style lang="scss" scoped>
    @import "../../assets/css/mixin";
    .content{
        background-color: $mtWhite;
        margin-top: 50px;
    }
    .content .form_wrapper{
        padding: 0 20px;
    }
    .con_row{
        display: flex;
        flex-direction: column;
        border-bottom: $mtBorder;
        justify-content: center;
    }
    .con_row:nth-child(4){
        margin-bottom: 10px;
    }
    input::-webkit-input-placeholder {
        color: #9B9B9B;
        font-size: 0.18rem;
    }
    .con_row input{
        padding: 20px 0;
        font-size: 0.18rem;
    }
    .prompt{
        color: #FFA900;
        font-size: 0.12rem;
    }
    .sumbit{
        width:96%;
        height: 52px;
        background: $mtBgYellow;
        margin: 0 auto;
        margin-top: 37px;
        border-radius: 8px;
        text-align: center;
        line-height: 52px;
        color: $mtWhite;
        font-size: 0.18rem;
    }
</style>





