<template>
    <section class="section register_bg">
        <div class="register">
            <div class="reg-title">
                <span class="iconfont icon-xiazai6" @click="goback"></span>
                <div class="search">
                    <input type="text" :placeholder="$t('doc.tel_search')" v-model="ruleForm.tel">
                    <span class="iconfont icon-sousuo"></span>
                </div>
            </div>
            <div class="reg_con">
                <div class="user">
                    <div class="">
                        <span>{{nickname}}</span>
                        <span>{{user}}</span>
                    </div>
                </div>
                <div class="user_list">
                    <ul>
                        <router-link :to="{name:'userRegister'}" tag="li">
                            {{$t('doc.register')}}
                        </router-link>
                        <router-link :to="{name:'userRegister'}" tag="li">
                            {{$t('doc.register')}}
                        </router-link>
                    </ul>
                </div>
                <div class="user_list_four">
                    <ul class="ul_fl">
                        <router-link :to="{name:'userRegister'}" tag="li">
                            {{$t('doc.register')}}
                        </router-link>
                        <router-link :to="{name:'userRegister'}" tag="li">
                            {{$t('doc.register')}}
                        </router-link>
                    </ul>
                    <ul class="ul_fr">
                        <router-link :to="{name:'userRegister'}" tag="li">
                            {{$t('doc.register')}}
                        </router-link>
                        <router-link :to="{name:'userRegister'}" tag="li">
                            {{$t('doc.register')}}
                        </router-link>
                    </ul>
                </div>
            </div>
        </div>
        <!-- <div class="sumbit" @click="sumbit">{{$t('doc.register')}}</div> -->
    </section>
</template>

<script>
    import LangSelect from '@/components/LangSelect';
    export default{
        components:{
            LangSelect
        },
        data(){
            return {
                nickname: '昵称',
                user: localStorage.getItem('mobile'),
                ruleForm: {
                    tel:'',
                }
            }
        },
        methods:{
            goback(){
                this.$router.go(-1)
            },
            sumbit(){
                this.$router.push({name:'userRegister'})
            }
            
        },
        created(){
            console.log(localStorage.getItem('mobile'))
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/mixin";
    .register_bg{
        background: $mtBg;
        background-size: cover;
        background-position: 50%;
        min-height: 100vh;
    }
    .register{
        padding: 0 10px;
    }
    .reg-title{
        /* 高度解决中英文切换高度错乱 */
        height: 50px;
        display: flex;
        box-sizing: border-box;
        align-items: center;
        margin-bottom: 40px;
    }
    .reg-title .icon-xiazai6{
        color: #fff;
        margin-right: 30px;
    }
    .reg-title .search{
        width: 70%;
        background: rgba(255, 255, 255, 0.1);
        display: flex;
        align-items: center;
        border-radius: 6px; 
    }
    .search input{
        width: 90%;
        background: transparent;
        padding: 8px 0 8px 8px;
    }
    .search .icon-sousuo{
        color: #fff;
        margin-right: 10px;
    }
    .reg_con .user{
        width: 234px;
        height: 110px;
        background: #f0f0f0;
        border-radius: 10px;
        margin: 0 auto;
        position: relative;
    }
    .reg_con .user::after{
        content: '';
        width: 300px;
        height: 132px;
        position: absolute;
        top: 50%;
        left: -15%;
        border: 1.5px dashed rgba(255,255,255,0.6);
    }
    .user_list {
        width: 234px;
        margin: 0 auto;
        margin-top: 50px;
    }
    .user_list ul{
        display: flex;
        justify-content: space-between;
    }
    .user_list ul li{
        width:92px;
        height: 57px;
        background: #f0f0f0;
        border-radius: 10px;
        text-align: center;
        line-height: 57px;
        color: $mtBlue;
        position: relative;
    }
    .user_list ul li::after{
        content: '';
        width: 1px;
        height: 57px;
        position: absolute;
        top: 50%;
        left: 50%;
        border-right: 1.5px dashed rgba(255,255,255,0.6);
    }
    .user_list_four{
        width: 100%;
        margin-top: 71px;
        display: flex;
        justify-content: space-between;
        position: relative;
    }
    .user_list_four ul{
        width: 49%;
        display: flex;
        justify-content: space-between;
    }
    .user_list_four ul::after{
        content: '';
        width: 90px;
        height: 60px;
        position: absolute;
        top: -87%;
        border: 1.5px dashed rgba(255,255,255,0.6);
        border-bottom: none;
    }
    .user_list_four .ul_fl::after{
        left: 10%;
    }
    .user_list_four .ul_fr::after{
        right: 10%;
    }
    .user_list_four ul li{
        width: 82px;
        height: 51px;
        background: #f0f0f0;
        border-radius: 10px;
        text-align: center;
        line-height: 51px;
        color: $mtBlue;
        position: relative;
    }
    .sumbit{
        width:82px;
        height: 52px;
        background: #f0f0f0;
        margin: 0 auto;
        margin-top: 37px;
        border-radius: 8px;
        text-align: center;
        line-height: 52px;
        color: $mtBlue;
        font-size: 0.18rem;
    }
</style>





