<template>
    <section class="section register_bg">
        <div class="register">
            <div class="reg-title">
                <h1>{{$t('doc.user_register')}}</h1>
                <span @click="goback">{{$t('doc.user_cancel')}}</span>
            </div>
            <div class="reg_con">
                <form action="">
                    <div class="con_row">
                        <i class="iconfont icon-shouji"></i>
                        <div class="con_col tel">
                            <span class="tel_add">+86 | </span>
                            <input type="text" :placeholder="$t('doc.user_tel')" v-model="ruleForm.tel">
                        </div>
                    </div>
                    <div class="con_row">
                        <i class="iconfont icon-user"></i>
                        <div class="con_col nickname">
                            <input type="text" :placeholder="$t('doc.user_nickname')" v-model="ruleForm.nickname">
                        </div>
                    </div>
                    <div class="con_row">
                        <i class="iconfont icon-mima"></i>
                        <div class="con_col login_pwd">
                            <input type="text" :placeholder="$t('doc.user_login_pwd')" v-model="ruleForm.login_pwd">
                        </div>
                    </div>
                    <div class="con_row">
                        <i class="iconfont icon-mima"></i>
                        <div class="con_col pay_pwd">
                            <input type="text" :placeholder="$t('doc.user_pay_pwd')" v-model="ruleForm.pay_pwd">
                        </div>
                    </div>
                    <div class="con_row">
                        <i class="iconfont icon-securityCode-b"></i>
                        <div class="con_col verify">
                            <input type="text" :placeholder="$t('doc.user_verify_code')" v-model="ruleForm.verify">
                            <span class="verify_code" @click="getCode">{{$t('doc.verify_code')}}</span>
                        </div>
                    </div>
                    <div class="sumbit" @click="sumbit">{{$t('doc.register_now')}}</div>
                </form>
            </div>
        </div>
        
    </section>
</template>

<script>
    import LangSelect from '@/components/LangSelect';
    import { Toast } from 'mint-ui';
    export default{
        components:{
            LangSelect,
            Toast
        },
        data(){
            return {
                ruleForm: {
                    tel:'',
                    nickname:'',
                    login_pwd:'',
                    pay_pwd:'',
                    verify:''
                }
            }
        },
        methods:{
            goback(){
                this.$router.go(-1)
            },
            getCode(){
                //axios请求 获取验证码的接口
            },
            sumbit(){
                const id=localStorage.getItem("id");
                let data = {
                    mobile: this.ruleForm.tel,
                    nick_name: this.ruleForm.nickname,
                    pwd: this.ruleForm.login_pwd,
                    pay_pwd: this.ruleForm.pay_pwd,
                    code:this.ruleForm.verify,
                    pid: id,
                }
                this.$axios.post("/user/register",data)
                .then(res=>{
                    console.log('--------')
                    console.log(res.data)
                    if(res.data.code==200){
                    //    this.$router.push('/home'); 
                    }else{
                        Toast({
                            message: res.data.msg,
                            position: 'bottom',
                        });
                    }
                })
            },
            
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/mixin";
    .register_bg{
        background: $mtBg;
        background-size: cover;
        background-position: 50%;
        min-height: 100vh;
    }
    .register{
        padding: 65px 8px 0;
    }
    .reg-title{
        /* 高度解决中英文切换高度错乱 */
        height: 42px;
        display: flex;
        box-sizing: border-box;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40px;
    }
    .reg-title h1{
        font-size: 0.3rem;
        color: $mtWhite;
    }
    .reg-title span{
        color: $mtWhite;
        margin-right: 30px;
    }
    .reg_con{
        width: 100%;
        height: 406px;
        background-color: $mtWhite;
        border-radius: 10px;
    }
    .reg_con form{
        padding: 0 15px;
    }
    .con_row {
        width: 100%;
        height: 60px;
        display: flex;
        align-items: center;
        border-bottom: $mtBorder;
    }
    .con_row .con_col{
        width: 100%;
        display: flex;
    }
    .con_col input{
        width: 100%;
    }
    .con_col .tel_add{
        margin-right: 10px;
    }
    .tel input{
        width: 80%;
    }
    .verify {
        justify-content: space-between;
    }
    .verify input {
        width: 65%;
    }
    .iconfont{
        color: $mtBlue;
        margin-right: 16px;
    }
    .verify_code{
        display: block;
        padding: 7px 18px;
        background: $mtBgYellow;
        border-radius: 6px;
        color: $mtWhite;
    }
    .sumbit{
        width:96%;
        height: 52px;
        background: $mtBgYellow;
        margin: 0 auto;
        margin-top: 37px;
        border-radius: 8px;
        text-align: center;
        line-height: 52px;
        color: $mtWhite;
        font-size: 0.18rem;
    }
</style>





